.news-generator {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.input-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

textarea {
  width: 100%;
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  background-color: #f0f0f0;
  resize: vertical;
}

.submit-button, .approve-button, .regenerate-button {
  padding: 12px 24px;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button, .approve-button {
  background-color: #0a0f4d;
  color: white;
  align-self: flex-start;
}

.regenerate-button {
  background-color: #666;
  color: white;
}

.submit-button:hover, .approve-button:hover {
  background-color: #161d7f;
}

.regenerate-button:hover {
  background-color: #888;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.news-editor {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.news-item {
  background-color: #f8f8f8;
  padding: 20px;
  border-radius: 8px;
}

.news-item h3 {
  margin-bottom: 10px;
  color: #333;
}

.day-title {
  background-color: #f8f8f8;
  padding: 20px;
  border-radius: 8px;
}

.action-buttons {
  display: flex;
  gap: 15px;
  margin-top: 20px;
}

.subtitle {
  color: #666;
  margin-bottom: 20px;
}

.loading-message {
  text-align: center;
  color: #666;
  font-style: italic;
}

.results-section {
  margin-top: 30px;
  padding: 20px;
  border-radius: 8px;
  background-color: #f8f8f8;
}

.generated-content {
  white-space: pre-wrap;
  line-height: 1.5;
}

.error-message {
  color: #dc3545;
  margin: 10px 0;
  padding: 10px;
  background-color: #f8d7da;
  border-radius: 4px;
  text-align: left;
} 